.login {
  display: flex;
  flex-direction: column;
  font-family: "Inter", sans-serif;

  .login-header {
    display: flex;
    flex-direction: row;
    gap: 10px;

    color: #1a427f;

    h4 {
      font-size: 24px;
      font-weight: 600;
    }
  }

  .login-body {
    padding-top: 15px;
  }

  @media (min-width: 1024px) {
    width: 40%;

    .login-header {
      display: flex;
      flex-direction: row;
      gap: 10px;

      color: #fff;
      background-color: #1a427f;
      padding: 24px 28px 24px;

      border-top-left-radius: 8px;
      border-top-right-radius: 8px;

      h4 {
        font-size: 18.2px;
        font-weight: 400;
      }
    }

    .login-body {
      display: flex;
      flex-direction: column;
      gap: 24px;

      padding: 24px;
      font-family: "Inter", sans-serif;

      border-left: 1px solid #c4c3c3;
      border-right: 1px solid #c4c3c3;
      border-bottom: 1px solid #c4c3c3;
      border-bottom-left-radius: 8px;
      border-bottom-right-radius: 8px;

      h5 {
        font-size: 20px;
        font-weight: 400;

        color: #002855;
      }

      p {
        font-size: 16px;
        line-height: 150%;
        color: #8f8f8f;
      }
    }
  }
}
